.hide {
  display: none !important;
}

.drawingtoolbar {
  margin-left: 0 !important;
}

.highlight-marker {
  opacity: 0.7;
}

.leaflet-pather {
  position: relative;
  z-index: 999;
}

.d-side-button {
  position: relative;
}
.d-side-button .extra-btn {
  min-width: 50px;
  max-height: 30px;
  position: absolute;
  top: 0;
  left: 32px;
  background: #5e5e5e;
  color: #fefefe;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
}
.d-side-button .extra-btn:hover {
  background: #6e6e6e;
  color: #fefefe;
}