.drawing-sidebar {
  /* class applies to select element itself, not a wrapper element */
}
.drawing-sidebar > * {
  margin-bottom: 15px !important;
}
.drawing-sidebar .textarea-wrapper {
  padding-top: 10px;
}
.drawing-sidebar .textarea-wrapper textarea {
  width: 100%;
  margin-top: 5px;
  min-height: 75px;
  box-sizing: border-box;
}
.drawing-sidebar input {
  margin-top: 5px;
  width: 100%;
  box-sizing: border-box;
}
.drawing-sidebar select {
  display: block;
  font-size: 16px;
  font-family: sans-serif;
  color: #333;
  padding: 0.4em 1.4em 0.4em 0.6em;
  width: 100%;
  max-width: 100%; /* useful when width is set to anything other than 100% */
  box-sizing: border-box;
  margin: 0;
  background-color: #fff;
}
.drawing-sidebar select option {
  font-weight: normal;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 100%;
}
.drawing-sidebar button {
  display: inline-block;
  padding: 0.3em 1.2em;
  margin: 0 0.3em 0.3em 0;
  border-radius: 2em;
  box-sizing: border-box;
  text-decoration: none;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: #ffffff;
  border: 1px solid #fff;
  background-color: #f14e4e;
  text-align: center;
  transition: all 0.2s;
  cursor: pointer;
  float: right;
}
.drawing-sidebar button:hover {
  background-color: #e43e3e;
}
.drawing-sidebar button:disabled,
.drawing-sidebar button[disabled] {
  background-color: #5c5c5c;
  cursor: default;
}
.drawing-sidebar .geovisto-input-autocomplete {
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 30px;
}
.drawing-sidebar .geovisto-input-autocomplete-label {
  padding-left: 5px;
  width: 100%;
  justify-content: flex-start;
}
.drawing-sidebar .geovisto-input-autocomplete-component {
  width: 100%;
}
.drawing-sidebar .geovisto-input-checkbox {
  display: flex;
  width: 100%;
}
.drawing-sidebar .geovisto-input-checkbox input {
  width: 100px;
}
.drawing-sidebar .geovisto-input-checkbox-label {
  width: 100%;
}
.drawing-sidebar .connect-check .geovisto-input-checkbox {
  height: 100px;
}
.drawing-sidebar .geovisto-input-range {
  display: flex;
  justify-content: center;
  align-items: center;
}
.drawing-sidebar .break {
  flex-basis: 100%;
  height: 0;
}
.drawing-sidebar .error-text {
  color: #ec0000;
}